import React from 'react';
import PropTypes from 'prop-types';
import { Container } from 'reactstrap';

export default class PageContent extends React.Component {
    static propTypes = {
        context: PropTypes.object.isRequired
    };
    render() {
        return(
            <main id="content">
                <Container>
                    <h1 className="display-4 my-5 my-lg-6 pb-lg-4 text-center">
                        {this.props.context.node_content.title[0].value}
                    </h1>
                </Container>
                <div className="bg-skew bg-skew-light">
                    <Container className="container">
                        <div className="content" dangerouslySetInnerHTML={{__html: this.props.context.node_content.body[0].value}}/>
                    </Container>
                </div>
            </main>
        )
    }
}