import React from 'react';
import { Link } from 'gatsby';
const alias = require('../data/api/alias');
const fr = require('../data/i18n/fr');
export default class StringHandler {
    /**
     * Create Menu Link Item
     * @param lang
     * @param item
     * @param classes
     * @returns {*}
     */
    static urlMenuString(lang, item, classes) {
        let default_value = <a href="" className={classes}>{item.title}</a>;
        if(item.link && item.link.uri) {
            let url =  item.link.uri;
            if(url.indexOf("entity:node/") !== -1) {
                url = parseInt(url.replace('entity:node/', ''));
                if(alias[lang] && alias[lang][url]) {
                    default_value = <Link to={alias[lang][url]} className={classes}>{item.title}</Link>;
                }
            }else if (url.indexOf("http") !== -1){
                default_value = <a href={item.link.uri} className={classes}>{item.title}</a>;
            }
        }
        return default_value;
    }

    /**
     * Redirect url to same page in other language
     * @param context
     * @param lang
     * @returns {string}
     */
    static redirectI18n(context, lang) {
        let link = '/'+lang;
        if(alias[lang] && alias[lang][context.nid]) {
            link = alias[lang][context.nid];
        }
        return link;
    }

    /**
     * Get String In Specific Lang
     * @param string
     * @param lang
     * @returns {*}
     */
    static stringI18n(string, lang) {
        if(lang === "fr" && fr[string]) {
            string = fr[string];
        }
        return string;
    }
}