import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import logo from '../../images/logo.png';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import StringHandler from '../../utils/string-handler';
import LanguageSwitcher from './languageSwitcher';

const languages = require('../../data/global/languages');
const menu_fr = require('../../data/api/menu--main-menu-french-fr');
const menu_en = require('../../data/api/menu--main-menu-english-en');

export default class Header extends Component {

    static propTypes = {
        context: PropTypes.object.isRequired
    };

    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    render() {
        let menus = this.props.context.lang === 'en' ? menu_en : menu_fr;
        let graphql_menu = languages.informations[this.props.context.lang].graphql_menu;
        return (
            <div>
                <header className="site-header">
                    <Navbar className="navbar navbar-expand-lg navbar-light py-3">
                        <div className="container">
                            <Link to={'/' + this.props.context.lang } className="logo-container navbar-brand ">
                                <img src={logo} alt="Parrot" className="logo-picture"/>
                            </Link>
                            <NavbarToggler onClick={this.toggle}/>
                            <Collapse isOpen={this.state.isOpen} navbar>
                                <Nav className="align-items-lg-center text-uppercase pt-3 pt-lg-0 ml-auto" navbar>
                                    {menus.map((nav) => (
                                        nav.children.length === 0 ? (
                                            <NavItem key={nav.id}>
                                                {StringHandler.urlMenuString(this.props.context.lang, nav.attributes, 'nav-link')}
                                            </NavItem>
                                        ) : (
                                            <UncontrolledDropdown nav inNavbar key={nav.id}>
                                                <DropdownToggle nav caret>
                                                    {nav.attributes.title}
                                                </DropdownToggle>
                                                <DropdownMenu right>
                                                    {nav.children.map((item) => (
                                                        <span key={item.id}>
                                                            {StringHandler.urlMenuString(this.props.context.lang, item.attributes, 'dropdown-item')}
                                                        </span>
                                                    ))}
                                                </DropdownMenu>
                                            </UncontrolledDropdown>
                                        )
                                    ))}
                                    <LanguageSwitcher context={this.props.context} position="top"/>
                                </Nav>
                            </Collapse>
                        </div>
                    </Navbar>
                </header>
            </div>
        )
    }
}