import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import StringHandler from '../../utils/string-handler';
import T from './translation';

import fr from '../../images/flags/fr.svg';
import en from '../../images/flags/en.svg';

const languages = require('../../data/global/languages');
const langs_flags = {fr: fr, en: en};


const mapObject = (lang, object, callback) => {
    return Object.keys(object).map(function (key) {
        if(key !== lang) {
            return callback(key, object[key]);
        }
    });
};

export default class LanguageSwitcher extends Component {

    static propTypes = {
        context: PropTypes.object.isRequired,
        position: PropTypes.string.isRequired
    };

    render() {
        return (
            <div>
                {this.props.position === 'top' ? (
                    <UncontrolledDropdown nav inNavbar>
                        <LanguageContent context={this.props.context} position={this.props.position}/>
                    </UncontrolledDropdown>
                ) : (
                    <UncontrolledDropdown>
                        <LanguageContent context={this.props.context} position={this.props.position}/>
                    </UncontrolledDropdown>
                )
                }
            </div>
        )
    }
}

const LanguageContent = ({ context, position }) => {
    return (
        <span>
                <DropdownToggle nav caret>
                    <span className="language-name">
                        <LanguageItem lang={context.lang}  type={position === 'top' ? 'small' : 'large'} />
                    </span>
                </DropdownToggle>
                <DropdownMenu right>
                    {mapObject(context.lang, languages.informations, function (key, value) {
                        return (
                            <Link key={key} className="language-name dropdown-item" to={StringHandler.redirectI18n(context, key)}>
                                <LanguageItem lang={key} type={'large'} />
                            </Link>)
                    })}
                </DropdownMenu>
        </span>
    );
};

const LanguageItem = ({ lang, type }) => {
    return (
        <span>
            <img src={langs_flags[lang]} className={"flag-header " + type} alt={lang} />
            {type === 'large' ? (
                <T message={languages.informations[lang].name}/>
            ) : null}
        </span>
    );
};