import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import logo from '../../images/logo.png';
import { NavItem} from 'reactstrap';
import _ from 'lodash';
import StringHandler from '../../utils/string-handler';
import LanguageSwitcher from './languageSwitcher';
import T from './translation';

const languages = require('../../data/global/languages');
const menu_fr = require('../../data/api/menu--footer-menu-fr-fr');
const menu_en = require('../../data/api/menu--footer-menu-en-en');

export default class Footer extends Component {

    static propTypes = {
        context: PropTypes.object.isRequired
    };

    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    render() {
        let menus = this.props.context.lang === 'en' ? menu_en : menu_fr;
        let graphql_menu = languages.informations[this.props.context.lang].graphql_menu;
        return (
            <div>
                <footer className="site-footer mt-5">
                    <div className="container">
                        <div className="row justify-content-md-between">
                            <div className="col-sm-12 col-md-4 mb-4 ">
                                <div className="footer-logo">
                                    <img src={logo} alt="Parrot" className="logo-picture"/>
                                </div>
                                <LanguageSwitcher context={this.props.context}  position="bottom"/>
                            </div>
                            <div className="col-sm-12 col-md-8 mb-8 ">
                                <h3 className="h5 mb-3">
                                    <T message="About Parrot"/>
                                </h3>
                                <div className="row">
                                    {_.chunk(menus, 2).map((menugroup, key) => (
                                        <div className="col-4 col-md-4 mb-4" key={key}>
                                            <ul className="nav flex-column">
                                                {menugroup.map((nav) => (
                                                    <li className="mb-1" key={nav.id}>
                                                        {StringHandler.urlMenuString(this.props.context.lang, nav.attributes, 'nav-link')}
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                        <hr className="m-0" />

                            <div
                                className="d-flex flex-column flex-sm-row justify-content-sm-between align-items-center mt-4">
                                <p className="small text-muted">Â© Parrot {(new Date()).getFullYear()}</p>
                            </div>
                    </div>
                </footer>
            </div>
        )
    }
}